<template>
  <div>

    <b-form-radio-group v-model="labelType" name="labelType">
      <b-form-radio value="share">Percent</b-form-radio>
      <b-form-radio value="pieces">Number of Coins</b-form-radio>
      <b-form-radio value="priceBGN">Value in levs</b-form-radio>
    </b-form-radio-group>

    <pie
      :data="pieData"
      :options="options"
      :width="500"
      :height="500">
    </pie>

  </div>
</template>


<script>
import Pie from './PieChart'

export default {
  props: ['assets'],
  components: {
    Pie,
  },
  data() {
    return {
      labelType: 'share',
      options: {
        responsive: false,
        // maintainAspectRatio: false,
      },
    }
  },
  computed: {
    pieData: function() {
      return {
        labels: this.assets.map(asset => asset.name),
        datasets: [
          {
            label: 'GitHub Commits',
            // backgroundColor: '#f87979',
            data: this.assets.map(asset => asset.share),
          },
        ],
      }
    },
  },
}
</script>



